import React, { useState, useEffect } from 'react';
import axios from 'axios';
import M from 'materialize-css';

export const Contact = () => {
    const [email, setEmail] = useState('');
    const [name, setName] = useState('');
    const [subject, setSubject] = useState('');
    const [message, setMessage] = useState('');

    useEffect(() => {
        M.AutoInit();
    })
    const submitForm = async (e: React.SyntheticEvent) => {
        e.preventDefault();

        if (!name || !email || !message) {

        } else {
            console.log('test');
            axios({
                method: "POST",
                url: 'https://shrouded-reef-83459.herokuapp.com/api/contact',
                data: {
                    name,
                    email,
                    subject,
                    message
                }
            }).then(function (response) {
                let tmp: any = response;
                if( tmp.data === 'failed') {
                    M.toast({ html: 'Failed to send message.'});
                } else {
                    M.toast({ html: 'Successfully sent message.'});
                }
            });
        }
    }

    return (
        <>
            <h2 className="contact-title">Contact Me</h2>
            <form onSubmit={(e) => submitForm(e)}>
                <div className="row">
                    <label htmlFor="name">Your Name(Required)</label><br />
                    <input className = "browser-default" id="name" type="text" value={name} onChange={(e) => setName(e.target.value)} />
                </div>

                <div className="row">
                    <label htmlFor="email">Your Email(Required)</label><br />
                    <input className = "browser-default" id="email" type="text" value={email} onChange={(e) => setEmail(e.target.value)} />
                </div>

                <div className="row">
                    <label htmlFor="subject">Subject</label><br />
                    <input className = "browser-default" id="subject" type="text" value={subject} onChange={(e) => setSubject(e.target.value)} />
                </div>

                <div className="row">
                    <label htmlFor="message">Your Message</label><br />
                    <textarea id="message" value={message} onChange={(e) => setMessage(e.target.value)}>
                        TEST
                    </textarea>
                </div>

                <div className="row">
                    <button className="submit" type="submit">SUBMIT</button>
                </div>
            </form>
        </>
    );
}